import { View } from '../router/View.js';

/**
 * Not Found View - Displayed when user navigates to unknown route
 */
export class NotFoundView implements View {
    public id = 'not-found';

    public init(container: HTMLElement): void {
        container.innerHTML = `
            <div class="not-found-container" style="
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                height: 100%;
                padding: 40px 20px;
                text-align: center;
                gap: 24px;
            ">
                <div style="font-size: 80px; line-height: 1;">ðŸš´</div>
                <h1 style="font-size: 48px; margin: 0; color: var(--color-text);">404</h1>
                <p style="font-size: 18px; margin: 0; color: var(--color-text-secondary);">
                    Page Not Found
                </p>
                <p style="font-size: 14px; margin: 0; color: var(--color-text-tertiary); max-width: 400px;">
                    The page you're looking for doesn't exist. It may have been moved or deleted.
                </p>
                <button 
                    id="notFoundHomeButton" 
                    class="btn btn-primary"
                    style="
                        padding: 12px 32px;
                        font-size: 16px;
                        border-radius: 8px;
                        border: none;
                        background-color: var(--interactive-default);
                        color: white;
                        cursor: pointer;
                        transition: background-color 0.2s;
                    "
                    aria-label="Go to dashboard"
                >
                    Go to Dashboard
                </button>
            </div>
        `;

        // Wire up button
        const homeButton = container.querySelector('#notFoundHomeButton');
        if (homeButton) {
            homeButton.addEventListener('click', () => {
                // Use window.router safely with optional chaining
                const router = (window as any).router;
                if (router && typeof router.navigate === 'function') {
                    router.navigate('/');
                }
            });
        }
    }

    public onEnter(): void {
        // Nothing to do on enter
    }

    public onLeave(): void {
        // Nothing to do on leave
    }
}
